@import url(https://fonts.googleapis.com/css?family=Coiny);
body {
  padding: 0;
  margin: 0; }

.wrapper {
  text-align: center;
  display: grid;
  place-items: center;
  min-height: 100vh;
  background-color: #5637BB;
  padding: 0 10px; }

svg {
  width: 150px;
  height: 150px;
  margin-top: -75px;
  margin-left: -75px;
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none; }

.tree {
  fill: #a50; }

.leaf-1 {
  fill: #4eb84e; }

.leaf-2 {
  fill: #48b448; }

.leaf-3 {
  fill: #45ac45; }

.leaf-4 {
  fill: #42a542; }

.leaf-5 {
  fill: #3f9e3f; }

.leaf-6 {
  fill: #3c973c; }

.leaf-7 {
  fill: #398f39; }

.leaf-8 {
  fill: #368836; }

.leaf-9 {
  fill: #338133; }

.leaf-10 {
  fill: #317931; }

.leaf-11 {
  fill: #2e722e; }

.animatedTextContainer {
  --maskX: 0;
  --maskY: 0;
  position: relative;
  cursor: default;
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none; }

.animatedTextContent, .animatedTextContentClone {
  color: #000000;
  font-size: 140px;
  font-weight: 700;
  font-family: 'Coiny', cursive;
  line-height: 1.1;
  letter-spacing: 5px;
  text-shadow: 5px 3px 8px rgba(0, 0, 0, 0.2);
  margin: 0; }

.animatedTextContentClone {
  position: absolute;
  left: 0;
  top: 0;
  color: #ffffff;
  -webkit-transition: all 0.4s ease-out;
  transition: all 0.4s ease-out;
  -webkit-clip-path: polygon(0 0, calc(var(--maskX) * 1% + (var(--maskY) - 50) * 0.4%) 0, calc(var(--maskX) * 1% + (var(--maskY) - 50) * -0.4%) 100%, 0 100%);
          clip-path: polygon(0 0, calc(var(--maskX) * 1% + (var(--maskY) - 50) * 0.4%) 0, calc(var(--maskX) * 1% + (var(--maskY) - 50) * -0.4%) 100%, 0 100%); }

